
.NavBar{
    background:rgb(9, 34, 58);
    color:white;
    padding:15px 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}



.NavBar h1 {
    font-size: 2rem;
}

.NavBar ul {
    list-style: none;
    text-decoration: none;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.Link {
    color:white;
    margin-right: 20px;
    font-size: 20px;
    text-decoration: none;
}



.Link:hover {
    text-decoration: none;
    color:rgb(199, 185, 185);
    
}

@media only screen and (max-width: 800px) {
    .NavBar{
        background:rgb(9, 34, 58);
        color:white;
        padding:15px 15px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .NavBar h1 {
        margin-bottom: 5px;
    }
    .NavBar ul {
     
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .NavBar li {
        margin-bottom: 5px;
    }
  }
.card{
    padding:10px;
    background: lightgrey;
    width:300px;
    text-align: left;
    margin:10px;
    border-radius: 3px;
    box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.274);
}
.card h3 {
    font-size: 1.5rem;
    text-align: center;
    font-weight: 600;
    
}

.card ul {
    margin:10px 0px;
    text-align: left;
}

.card ul li {
    margin:10px 0px;
}
.badge{
    font-size: 1rem;
    color:white;
    padding:5px 7px 0px 7px;
   
    border-radius: 3px;
}

.badge-secondary{
    background: rgb(109, 109, 201);;
}

.badge-primary{
    background: rgb(91, 184, 91);
}

.card button {
    padding:3px 10px;
    margin:3px;
    color:white;
}

.edit_button{
    background: rgb(9, 34, 58);
}

.delete_button{
    background: rgba(110, 46, 4, 0.877);
}

.noContacts{
    display: flex;
    flex-direction: column;
}

.noContacts h2{
    font-size: 2rem;
    margin-bottom: 7px;
}

.noContacts p{
    font-size: 1.3rem;
    color:rgb(6, 6, 88);
}


.spinner {
    margin:0 auto;
    position:absolute;
    top:50;
    left:50;

}



.filterInput{
    padding:7px;
    margin-bottom:20px;
}
.Home{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}
.about-paragraph{
    background:rgb(105, 105, 212);
    padding:7px;
    color:white;
}
.add-contact{
    width:400px;
    display: flex;
    flex-direction: column;
    margin:0 auto;

    padding:10px;
}

.add-contact input{
    margin:10px 0px;
    padding:10px;
  
}

.add-contact h5 {
    font-size: 1.7rem;
}

.add-contact div{
    width:100px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.add-contact input[type="radio"]{
    margin-right: 10px;
}

.add-contact button{
    margin-top: 10px;
    padding:5px;
}
.register-container{
    display: flex;
    flex-direction: column;
    margin:0 auto;
    max-width: 400px;
}

.register-container input{
    padding:5px;
    margin-bottom:10px;
}

.register-container label {
    font-size: 1.2rem;
    margin-bottom: 5px;
}

.register-wrapper h1{
    font-size: 1.8rem;
    margin-bottom: 20px;
}

.register-container button {
    color:white;
    font-size: 1.5rem;
    padding:7px;
    background:#0069ed;
}

.login-container{
    display: flex;
    flex-direction: column;
    margin:0 auto;
    max-width: 400px;
}

.login-container input{
    padding:5px;
    margin-bottom:10px;
}

.login-container label {
    font-size: 1.2rem;
    margin-bottom: 5px;
}

.login-wrapper h1{
    font-size: 1.8rem;
    margin-bottom: 20px;
}

.login-container button {
    color:white;
    font-size: 1.5rem;
    padding:7px;
    background:#0069ed;
}



.alertRed{
    position:relative;
    top:0;
    left:0;
    width:100%;
    background:
    -webkit-gradient(linear , left top, right top , from(transparent), color-stop(rgb(134, 53, 6)), to(transparent));
    background:
    linear-gradient(to right , transparent, rgb(134, 53, 6), transparent);
    margin-bottom: 20px;
    background-blend-mode: screen;
    padding:10px;
    color:white;
    font-size: 1.5rem;
}
/* http://meyerweb.com/eric/tools/css/reset/
